import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "BottomMenu",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    /*校验手机号*/
    let checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("手机号不能为空"));
      } else {
        if (/^[1][345789]\d{9}$/.test(value)) {
          callback();
        } else {
          return callback(new Error("请输入正确的手机号"));
        }
      }
    };
    return {
      isEditCheckChannel: true,
      pagesize: 5,
      currentPage: 1,
      total: 0,
      player_id: "", // 玩家id
      labelPosition: "left", //左对齐
      dialogFormVisible: false,
      dialogTitle: "",
      platforms: [
        { value: 1, label: "平台1" },
        { value: 2, label: "平台2" }
      ],
      format: {
        platform: ""
      },
      ChannelList: [], //渠道列表
      checkChannelCode: "",
      channel_code: "",
      funOpts: [],
      tableData: [],
      records: [],
      pageInfo: new PageInfo(0, [5, 10, 15], 5),
      dialogAddVisible: false,
      form: {
        id: null,
        checkList: [],
        function1: "留空",
        function2: "留空",
        function3: "留空",
        function4: "留空",
        function5: "留空",
        function6: "留空"
      }
    };
  },
  methods: {
    getBottomMenuList() {
      this.$http
        .get("v1/backend/lobby/bottom", {
          params: {
            page: this.currentPage,
            limit: this.pagesize,
            type: 1
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.tableData = res.data.data;
            this.total = res.data.total
            this.func_list_index();
          console.log(this.tableData)
          }
        });
    },
    func_list_index: function() {
      this.tableData.map((item1, index1) => {
        JSON.parse(item1.func_list).map((item2,index2) => {
          item1[`func_${index2+1}`] =  item2
        })
      });
    },
    resetForm() {
      this.form = {
        id: null,
        checkList: [],
        function1: "留空",
        function2: "留空",
        function3: "留空",
        function4: "留空",
        function5: "留空",
        function6: "留空"
      };
    },
    openAddDialog() {
      this.dialogFormVisible = true;
      this.dialogTitle = "添加活动入口配置";
      this.resetForm();
      this.getChannelList();
      this.getFunNameList();
    },
    //获取功能名字列表
    getFunNameList() {
      this.$http
        .get("v1/backend/lobby/name_type", {
          params: {
            type_id: 3
          }
        })
        .then(res => {
          console.log(res);
          this.funOpts = res.data.data;
        });
    },
    //获取渠道选项列表
    getChannelList() {
      let data = {
        type_id: 1,
          add_id: 3
      }
      this.$http.post("v1/backend/no_channel",data).then(res => {
        console.log(res);
        this.ChannelList = res.data.data;
      });
    },
    //得到选中的渠道的channel-code
    getChannleCode() {
      this.ChannelList.forEach((item, index) => {
        console.log(item);
        console.log(this.form.checkList);
        if (this.form.checkList[0] === item.channel_name) {
          this.checkChannelCode = item.channel_code;
          console.log(this.checkChannelCode);
        }
      });
    },
    addChannelFun() {
      if (!this.form.id) {
        let funStr =
          this.form.function1 +
          "," +
          this.form.function2 +
          "," +
          this.form.function3 +
          "," +
          this.form.function4 +
          "," +
          this.form.function5 +
          "," +
          this.form.function6;
        let funArr = funStr.split(",");
        let data = {
          name: this.form.checkList[0],
          code: this.checkChannelCode,
          list_id: JSON.stringify(funArr),
          type: 1
        };
        this.$http.post("v1/backend/lobby/bottom", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getBottomMenuList();
          }
        });
      } else {
        console.log("wojinlaile ");
        let funStr =
          this.form.function1 +
          "," +
          this.form.function2 +
          "," +
          this.form.function3 +
          "," +
          this.form.function4 +
          "," +
          this.form.function5 +
          "," +
          this.form.function6;
        let funArr = funStr.split(",");
        let data = {
          banner_id: this.form.id,
          name: this.form.checkList[0],
          code: this.form.checkList[0],
          list_id: JSON.stringify(funArr),
          type: 1
        };
        console.log(data);
        this.$http.put("v1/backend/lobby/bottom", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getBottomMenuList();
          }
        });
      }
    },
    handleEdit(row) {
      console.log(row);
      this.getChannelList();
      this.getFunNameList();
      this.dialogFormVisible = true;
      this.dialogTitle = "更新活动入口配置";
      this.form.checkList = [row.channel_name];
      this.form.id = row.id;
      this.form.function1 = JSON.parse(row.func_list)[0];
      this.form.function2 = JSON.parse(row.func_list)[1];
      this.form.function3 = JSON.parse(row.func_list)[2];
      this.form.function4 = JSON.parse(row.func_list)[3];
      this.form.function5 = JSON.parse(row.func_list)[4];
      this.form.function6 = JSON.parse(row.func_list)[5];
    },
    handleDelete(row) {
      console.log(row);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/lobby/bottom", {
              params: {
                id: row.id
              }
            })
            .then(res => {
              if (res.data.code === 1) {
                this.getBottomMenuList();
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getBottomMenuList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getBottomMenuList();
    },
    /**搜索*/
    search() {
      let data = this.format,
        user_id = 1000;
      this.userList(data, user_id);
    },
    /** 添加会员 */
    addUser() {
      this.dialogAddVisible = true;
    },
    handelClick(btn, row) {
      if (btn.type === "edit") {
        this.dialogFormVisible = true;
      }
    }
  },
  watch: {
    "form.checkList": function(newVal, oldVal) {
      console.log(newVal, oldVal);
      if (newVal) {
        this.getChannleCode();
      }
    }
  },
  mounted() {
    this.getBottomMenuList();
  }
};



